    url('../fonts/icomoon.ttf?ez60bw') format('truetype'),
    url('../fonts/icomoon.woff?ez60bw') format('woff'),
    url('../fonts/icomoon.svg?ez60bw#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
